// src/components/LoginPage.jsx
import React, { useState } from "react";

const API_BASE_URL = import.meta?.env?.VITE_API_URL || "https://son-of-mervan-production.up.railway.app";

export default function LoginPage({ onLogin }) {
  const [mode, setMode] = useState("login"); // "login" | "signup"
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [submitting, setSubmitting] = useState(false);
  const [message, setMessage] = useState(null); // success/info/error text
  const [isError, setIsError] = useState(false);

  const goToSignup = (e) => {
    e?.preventDefault();
    setMessage(null);
    setIsError(false);
    setMode("signup");
  };

  const goToLogin = (e) => {
    e?.preventDefault();
    setMessage(null);
    setIsError(false);
    setMode("login");
  };

  const doLogin = async (e) => {
    e.preventDefault();
    setSubmitting(true);
    setIsError(false);
    setMessage(null);
    try {
      const res = await fetch(`${API_BASE_URL}/login`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ username, password }),
      });
      if (!res.ok) {
        const body = await res.json().catch(() => ({}));
        throw new Error(body.detail || "Login failed");
      }
      const data = await res.json();
      onLogin?.(data.access_token);
    } catch (err) {
      setIsError(true);
      setMessage(err.message || "Login failed");
    } finally {
      setSubmitting(false);
    }
  };

  const doSignup = async (e) => {
    e.preventDefault();
    setSubmitting(true);
    setIsError(false);
    setMessage(null);
    try {
      // Basic front-end guardrails (your backend enforces real policy)
      if (password.length < 8) {
        throw new Error("Password must be at least 8 characters.");
      }
      const res = await fetch(`${API_BASE_URL}/signup`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ username: username.trim(), password }),
      });
      if (!res.ok) {
        const body = await res.json().catch(() => ({}));
        throw new Error(body.detail || "Signup failed");
      }
      // Success  switch to login with nice message
      setMode("login");
      setIsError(false);
      setMessage("Account created! Please log in.");
    } catch (err) {
      setIsError(true);
      setMessage(err.message || "Signup failed");
    } finally {
      setSubmitting(false);
    }
  };

  const headline = mode === "login" ? "Welcome back" : "Create your account";
  const cta = mode === "login" ? "Log in" : "Sign up";

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-900 via-purple-900 to-indigo-900 flex items-center justify-center px-4">
      <div className="w-full max-w-md bg-white/95 backdrop-blur rounded-2xl shadow-xl p-8">
        <h1 className="text-2xl font-bold text-gray-900 mb-2">{headline}</h1>
        <p className="text-gray-600 mb-6">
          {mode === "login" ? "Use your credentials to access SYITB." : "Pick a unique username and a strong password."}
        </p>

        {message && (
          <div
            className={`mb-4 rounded-lg px-4 py-3 text-sm ${
              isError ? "bg-red-50 text-red-700 border border-red-200" : "bg-green-50 text-green-700 border border-green-200"
            }`}
          >
            {message}
          </div>
        )}

        <form onSubmit={mode === "login" ? doLogin : doSignup} className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Username</label>
            <input
              type="text"
              autoComplete="username"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              className="w-full px-4 py-2 border-2 border-gray-200 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition"
              placeholder="yourname"
              required
            />
          </div>

          <div>
            <div className="flex items-center justify-between">
              <label className="block text-sm font-medium text-gray-700 mb-1">Password</label>
              {mode === "signup" && (
                <span className="text-xs text-gray-500">8+ chars, upper, lower, number, special</span>
              )}
            </div>
            <input
              type="password"
              autoComplete={mode === "login" ? "current-password" : "new-password"}
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="w-full px-4 py-2 border-2 border-gray-200 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition"
              placeholder={mode === "login" ? "" : "Create a strong password"}
              required
            />
          </div>

          <button
            type="submit"
            disabled={submitting}
            className="w-full bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 text-white font-semibold py-3 rounded-xl shadow-lg transition disabled:opacity-60"
          >
            {submitting ? (mode === "login" ? "Logging in" : "Creating account") : cta}
          </button>
        </form>

        <div className="mt-6 text-center text-sm text-gray-600">
          {mode === "login" ? (
            <>
              New here?{" "}
              <button onClick={goToSignup} className="font-semibold text-blue-600 hover:text-blue-800">
                Create an account
              </button>
            </>
          ) : (
            <>
              Already have an account?{" "}
              <button onClick={goToLogin} className="font-semibold text-blue-600 hover:text-blue-800">
                Log in
              </button>
            </>
          )}
        </div>
      </div>
    </div>
  );
}
